
.grid-root {
  display: grid;
}
@media (min-width: 665px){
.grid-root {
    grid-template-columns: -webkit-max-content -webkit-max-content auto -webkit-max-content;
    grid-template-columns: max-content max-content auto max-content;
    grid-template-rows: -webkit-max-content -webkit-max-content auto;
    grid-template-rows: max-content max-content auto;
    grid-template-areas:
      "names    group   group     var"
      "map      map     controls  controls"
      "results  results results   results"
      "login    login   login     login";
}
}
@media (max-width: 664px){
.grid-root {
    grid-template-columns: -webkit-max-content auto -webkit-max-content;
    grid-template-columns: max-content auto max-content;
    grid-template-rows: -webkit-max-content -webkit-max-content -webkit-max-content -webkit-max-content auto;
    grid-template-rows: max-content max-content max-content max-content auto;
    grid-template-areas:
      "names    group     var"
      ".        .         ."
      "map      map       map"
      "controls controls  controls"
      "results  results   results"
      "login    login     login";
}
}

.incorrect{
  border-color: red;
}
